import React from "react";
import { useNavigate } from "react-router-dom";
import {
  Sparkles,
  ShieldCheck,
  Clock3,
  MessageCircle,
  ArrowRight,
} from "lucide-react";

const highlights = [
  {
    title: "Admissions & Academics",
    description:
      "Instant answers on programs, intakes, credit transfers, and faculty contacts.",
  },
  {
    title: "Campus Life & Services",
    description:
      "Guidance on hostels, transport, dining, clubs, and student support offices.",
  },
  {
    title: "Smart Admin Support",
    description:
      "Admins can review conversations, manage knowledge, and monitor performance in real time.",
  },
];

const stats = [
  { label: "Average response time", value: "1.8s" },
  { label: "Knowledge entries", value: "600+" },
  { label: "Live conversations", value: "24/7" },
];

const steps = [
  "Explore BU programs, fees, and admissions.",
  "Ask about life on campus and support services.",
  "Login for unlimited chats & saved history.",
];

function LandingPage() {
  const navigate = useNavigate();

  return (
    <div className="min-h-screen bg-slate-950 text-white relative overflow-hidden">
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_top,_rgba(59,130,246,0.35),_transparent_60%)] pointer-events-none" />
      <nav className="relative z-10 max-w-6xl mx-auto px-6 pt-6 pb-2 flex flex-wrap items-center gap-4 justify-between">
        <div className="flex items-center gap-3">
          <div className="w-12 h-12 rounded-2xl bg-white/10 border border-white/20 flex items-center justify-center shadow-lg shadow-blue-900/40">
            <Sparkles className="w-5 h-5" />
          </div>
          <div>
            <p className="text-xs uppercase tracking-[0.4em] text-white/60">Bugema University</p>
            <p className="text-lg font-semibold">AI Engagement Suite</p>
          </div>
        </div>
        <div className="flex items-center gap-2 text-sm">
          <button
            onClick={() => navigate("/chatbot")}
            className="hidden sm:inline-flex items-center gap-2 px-4 py-2 rounded-2xl border border-white/15 text-white/90 hover:bg-white/10 transition"
          >
            <MessageCircle className="w-4 h-4" />
            Chat
          </button>
          <button
            onClick={() => navigate("/login")}
            className="hidden sm:inline-flex items-center gap-2 px-4 py-2 rounded-2xl border border-white/15 text-white/90 hover:bg-white/10 transition"
          >
            <ShieldCheck className="w-4 h-4" />
            Log in
          </button>
          <button
            onClick={() => navigate("/signup")}
            className="inline-flex items-center gap-2 px-4 py-2 rounded-2xl bg-white text-slate-900 font-semibold shadow-lg shadow-blue-900/30"
          >
            Join now
          </button>
        </div>
      </nav>
      <header className="relative z-10 max-w-6xl mx-auto px-6 pt-12 pb-8 flex flex-col gap-6 lg:flex-row lg:items-center">
        <div className="flex-1">
          <p className="inline-flex items-center gap-2 text-xs uppercase tracking-[0.4em] text-white/60">
            <Sparkles className="w-4 h-4" />
            Bugema University
          </p>
          <h1 className="text-4xl lg:text-5xl font-semibold leading-tight mt-4 font-display">
            Personalized chatbot support for every Bugema University journey.
          </h1>
          <p className="text-lg text-white/80 mt-4 max-w-2xl">
            BU Chatbot blends curated knowledge with live AI to guide students, parents,
            and guestsanytime, on any device.
          </p>
          <div className="mt-8 flex flex-wrap gap-3">
            <button
              onClick={() => navigate("/chatbot")}
              className="inline-flex items-center gap-2 px-6 py-3 rounded-2xl bg-white text-slate-900 font-semibold shadow-lg shadow-blue-900/40"
            >
              Start chatting
              <ArrowRight className="w-4 h-4" />
            </button>
            <button
              onClick={() => navigate("/admin")}
              className="inline-flex items-center gap-2 px-6 py-3 rounded-2xl border border-white/30 text-white/80 font-semibold hover:bg-white/10 transition"
            >
              Admin console
            </button>
          </div>
          <div className="mt-8 grid grid-cols-1 sm:grid-cols-3 gap-3">
            {stats.map((item) => (
              <div
                key={item.label}
                className="rounded-2xl border border-white/10 bg-white/5 px-4 py-3"
              >
                <p className="text-2xl font-semibold text-white">{item.value}</p>
                <p className="text-xs uppercase tracking-wide text-white/60">
                  {item.label}
                </p>
              </div>
            ))}
          </div>
        </div>
        <div className="flex-1">
          <div className="bg-white/5 border border-white/10 rounded-3xl p-6 shadow-2xl shadow-blue-900/40 space-y-4">
            <div className="flex items-center gap-4">
              <div className="w-14 h-14 rounded-2xl bg-gradient-to-br from-blue-400 to-indigo-500 flex items-center justify-center">
                <MessageCircle className="w-6 h-6" />
              </div>
              <div>
                <p className="text-white/70 text-sm">Live assistant</p>
                <p className="text-white font-semibold text-lg">BU Campus AI</p>
              </div>
            </div>
            <div className="space-y-3">
              <div className="rounded-2xl bg-black/30 border border-white/10 p-4">
                Hello! I'm here to help you explore academic programs, fees, deadlines, and
                everything happening on campus.
              </div>
              <div className="rounded-2xl bg-white text-slate-900 p-4">
                Great! Tell me what I need for January intake.
              </div>
              <div className="rounded-2xl bg-black/30 border border-white/10 p-4">
                You'll need your academic transcripts, national ID, and the filled
                application form. Let me send you the checklist.
              </div>
            </div>
          </div>
        </div>
      </header>

      <main className="relative z-10 max-w-6xl mx-auto px-6 pb-16 space-y-12">
        <section className="grid gap-6 md:grid-cols-3">
          {highlights.map((item) => (
            <div
              key={item.title}
              className="rounded-3xl bg-white/5 border border-white/10 p-6 hover:bg-white/10 transition"
            >
              <ShieldCheck className="w-6 h-6 text-white/70" />
              <h3 className="mt-4 text-xl font-semibold">{item.title}</h3>
              <p className="text-white/70 text-sm mt-2 leading-relaxed">{item.description}</p>
            </div>
          ))}
        </section>

        <section className="rounded-3xl bg-white text-slate-900 p-6 md:p-10 shadow-2xl shadow-black/20">
          <div className="flex flex-col lg:flex-row gap-8 lg:items-center">
            <div className="flex-1">
              <p className="text-xs uppercase tracking-[0.4em] text-slate-500 flex items-center gap-2">
                <Clock3 className="w-4 h-4" />
                How it works
              </p>
              <h2 className="text-3xl font-semibold mt-4">
                Three simple steps to get the answers you need.
              </h2>
              <p className="text-slate-600 mt-3">
                BU Chatbot pairs curated knowledge with live search so the information you
                receive is up-to-date and trustworthy.
              </p>
            </div>
            <div className="flex-1 space-y-4">
              {steps.map((step, index) => (
                <div key={step} className="flex gap-4">
                  <span className="w-10 h-10 rounded-2xl bg-slate-900 text-white flex items-center justify-center font-semibold">
                    {index + 1}
                  </span>
                  <p className="text-lg font-medium text-slate-800">{step}</p>
                </div>
              ))}
            </div>
          </div>
        </section>
      </main>
    </div>
  );
}

export default LandingPage;