// src/views/Admin/AdminLayout.js
import React, { useState } from "react";
import { Outlet, NavLink } from "react-router-dom";
import {
  LogOut,
  Users,
  BookOpen,
  Search,
  Settings,
  BarChart2,
  MessageSquare,
  Menu,
  X,
} from "lucide-react";
import { Button } from "../../components/ui/Button";

export default function AdminLayout({ onLogout }) {
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const navItems = [
    { name: "Dashboard", path: "/admin/dashboard", icon: BarChart2 },
    { name: "Conversations", path: "/admin/conversations", icon: Search },
    { name: "FAQs", path: "/admin/faqs", icon: BookOpen },
    { name: "Knowledge Base", path: "/admin/knowledge", icon: BookOpen },
    { name: "Users", path: "/admin/users", icon: Users },
    { name: "Admins", path: "/admin/admins", icon: Users },
    { name: "Analytics", path: "/admin/analytics", icon: BarChart2 },
    { name: "Settings", path: "/admin/settings", icon: Settings },
  ];

  const adminName =
    (typeof window !== "undefined" && localStorage.getItem("adminName")) ||
    "Administrator";

  return (
    <div className="min-h-screen bg-slate-100 flex">
      {/* Sidebar */}
      <aside
        className={`fixed inset-y-0 left-0 z-40 w-72 bg-slate-900 text-white shadow-2xl shadow-slate-900/40 transform transition-transform duration-300 lg:static lg:translate-x-0 ${
          sidebarOpen ? "translate-x-0" : "-translate-x-full lg:translate-x-0"
        }`}
      >
        <div className="p-6 border-b border-white/10 flex items-center justify-between">
          <div>
            <p className="text-xs uppercase tracking-[0.4em] text-white/60">
              Bugema University
            </p>
            <p className="text-xl font-semibold">Admin Console</p>
          </div>
          <button
            className="lg:hidden p-2 rounded-xl bg-white/10 hover:bg-white/15"
            onClick={() => setSidebarOpen(false)}
          >
            <X className="w-4 h-4" />
          </button>
        </div>
        <nav className="flex-1 p-4 space-y-2 overflow-y-auto">
          {navItems.map(({ name, path, icon: Icon }) => (
            <NavLink
              key={path}
              to={path}
              className={({ isActive }) =>
                `flex items-center gap-3 rounded-2xl px-4 py-3 transition ${
                  isActive
                    ? "bg-white text-slate-900 font-semibold shadow-lg shadow-slate-900/20"
                    : "text-white/70 hover:bg-white/10"
                }`
              }
              onClick={() => setSidebarOpen(false)}
            >
              <Icon className="w-4 h-4" />
              {name}
            </NavLink>
          ))}
        </nav>
        <div className="p-4 border-t border-white/10">
          <Button
            onClick={onLogout}
            className="flex items-center gap-2 w-full justify-center bg-red-500 hover:bg-red-600"
          >
            <LogOut className="w-4 h-4" /> Logout
          </Button>
        </div>
      </aside>

      {/* Main Content */}
      <div className="flex-1 flex flex-col lg:ml-0">
        <header className="sticky top-0 z-30 bg-white/90 backdrop-blur border-b border-slate-200 px-4 lg:px-8 py-4 flex items-center justify-between gap-4 shadow-sm">
          <div className="flex items-center gap-3">
            <button
              className="lg:hidden p-2 rounded-2xl bg-slate-900 text-white"
              onClick={() => setSidebarOpen((prev) => !prev)}
            >
              <Menu className="w-5 h-5" />
            </button>
            <div>
              <p className="text-xs uppercase tracking-[0.4em] text-slate-500">
                Admin workspace
              </p>
              <p className="text-lg font-semibold text-slate-900">
                Welcome back, {adminName}
              </p>
            </div>
          </div>
          <div className="flex items-center gap-3">
            <a
              href="/chatbot"
              className="hidden md:inline-flex items-center gap-2 px-4 py-2 rounded-2xl border border-slate-200 text-slate-600 hover:bg-slate-50"
            >
              <MessageSquare className="w-4 h-4" />
              View chatbot
            </a>
            <button
              onClick={onLogout}
              className="inline-flex items-center gap-2 px-4 py-2 rounded-2xl bg-slate-900 text-white font-semibold"
            >
              <LogOut className="w-4 h-4" />
              Sign out
            </button>
          </div>
        </header>
        <main className="flex-1 p-4 lg:p-8 overflow-auto">
          <Outlet />
        </main>
      </div>

    </div>
  );
}
